import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


const Storage = {
  save(data){
    localStorage.setItem('vue-todo', JSON.stringify(data))
  },
  load(){
    return JSON.parse(localStorage.getItem('vue-todo') || '[]')
  }
}

const TodoFilter = {
  All(TodoData){
    return TodoData
  },
  Finished(TodoData){
    return TodoData.filter(todo => {
      return todo.complete
    })
  },
  Unfinished(TodoData){
    return TodoData.filter(todo => {
      return !todo.complete
    })
  }
}

export default new Vuex.Store({
  // strict: true,
  state: {
    TodoData: [
      {content:'thing1', complete: false},
      {content:'thing2', complete: true},
      {content:'thing3', complete: false}
    ]
  },
  getters: {
    CreateTodoIndex(state){
      return TodoFilter[state.route.name](state.TodoData).map(item => state.TodoData.indexOf(item))
    }
  },
  mutations: {
    SetList(state, data){
      state.TodoData = data
      Storage.save(state.TodoData)
    },
    AddList(state, data){
      state.TodoData.push(data)
      Storage.save(state.TodoData)
    },
    UpdateList(state, {index , data}){
      state.TodoData[index].content = data.content
      state.TodoData[index].complete = data.complete
      Storage.save(state.TodoData)
    },
    DeleteList(state , index){
      state.TodoData.splice(index, 1)
      Storage.save(state.TodoData)
    },
  },
  actions: {
    InitList({commit}){
      commit('SetList', Storage.load())
    }
  },
  modules: {
  }
})
