<template>
    <div class="todoinputBox">
        <input 
            type="text" 
            id="inputBlank" 
            placeholder="請輸入待辦事項"
            v-model.trim="todo"
            @keyup.enter="submitHandler"
            maxlength="20"
        />
    </div>
</template>



<script>
export default {
    data(){
        return{
            todo: ''
        }
    },
    methods:{
        submitHandler(){
            if(!this.todo) return
            this.$store.commit('AddList',{
                content:this.todo,
                complete:false
            })
            this.todo = ''
        }
    }
}
</script>




<style>
#inputBlank{
    width: 300px;
    height: 40px;
    font-size: 30px;
}
</style>