<template>
    <div class="todoitemBox">
        <label class="checkLabel">
            <input 
                type="checkbox" 
                v-model="Complete" 
                id="checkinput"
                name="checkinput1"    
            >
        </label>
        <input 
            type="text" 
            v-model.trim="tmpData" 
            v-if="tmpData" 
            class="contentedit" 
            maxlength="20"
            v-focus
            @keyup.enter="updateHandler()"
            @keyup.esc="cancelHandler()"
            @blur="cancelHandler()"
        >
        <label id="labelcontent" @dblclick="editHandler()" v-else>
            {{ GetTodoData.content }}
        </label>
        <button id="buttondelete" @click="destroyHandler()">╳</button>
    </div>
</template>

<script>
export default {
    props:{
        index:{
            type: Number,
            required: true
        },
    },
    data() {
        return{
            tmpData: ''
        }
    },
    computed:{
        GetTodoData(){
            return this.$store.state.TodoData[this.index]
        },
        Complete: {
            get(){
                return this.GetTodoData.complete
             },
            set(val){
                this.$store.commit('UpdateList',{
                    index:this.index,
                    data: {
                        content: this.GetTodoData.content,
                        complete: val
                    }
                })
            }
        },
    },
    methods:{
        destroyHandler() {
            if(confirm(`是否確認刪除 ${this.GetTodoData.content} ?` ))
            this.$store.commit('DeleteList',this.index)
        },

        editHandler() {
            this.tmpData = this.GetTodoData.content
        },

        updateHandler() {
            this.$store.commit('UpdateList',{
                index:this.index,
                data: {
                    content: this.tmpData,
                    complete: this.GetTodoData.complete
                }
            })
            this.tmpData = ''
        },

        cancelHandler() {
            this.tmpData = ''
        }
    }
}
</script>

<style>
.todoitemBox{
    position: relative;
    border: 1px solid #aaa;
    background-color: white;
    width: 500px;
    height: 50px;
    margin: 20px auto 0 auto;
    font-size: 20px;
    line-height: 50px;
    border-radius: 15px;
}

.checkLabel{
    position: absolute;
    left: 10px;
}
#checkinput {
    width: 20px;
    height: 20px;
}

.contentedit{
    position: absolute;
    top: 7px;
    left: 40px;
    font-size: 20px;
    width: 350px;
    height: 30px;
}
#labelcontent{
    position: absolute;
    left: 40px;
}
.ellipsis{
    margin: auto;
    overflow:hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

#buttondelete{
    position: absolute;
    right: 15px;
    top: 10px;
    background-color: tomato;
}

@media screen and (max-width: 768px){
    .todoitemBox{
    width: 370px;
    font-size: 14px;
    }
    .checkLabel{
    position: absolute;
    left: 10px;
    top: 5px;
}
}
</style>